/* You can add global styles to this file, and also import other style files */
/*================================================ Default CSS =================================================*/
@import "tailwindcss/base";
@import "tailwindcss/components";
@import "tailwindcss/utilities";

:root {
    --fontFamily: 'Open Sans', sans-serif;
    --headingFontFamily: 'Inter', sans-serif;
    --mainColor: #c44118;
    --optionalColor: #666;
    --whiteColor: #fff;
    --blackColor: #0e0129;
    --fontSize: 15px;
    --transition: 0.5s;
}
body {
    margin: 0;
    padding: 0;
    color: var(--blackColor);
    background-color: #f1f1f1;
    font-size: var(--fontSize);
    font-family: var(--fontFamily);
}
.h1, .h2, .h3, .h4, .h5, .h6, h1, h2, h3, h4, h5, h6 {
    color: var(--blackColor);
    font-weight: 700;
    font-family: var(--headingFontFamily);
}
a {
    color: var(--blackColor);
    transition: var(--transition);
    text-decoration: none;
    outline: 0 !important;
}
a:hover {
    color: var(--mainColor);
    text-decoration: none;
}
:focus {
    outline: 0 !important;
}
.d-table {
    width: 100%;
    height: 100%;
}
.d-table-cell {
    vertical-align: middle;
}
img {
    max-width: 100%;
    height: auto;
}
p {
    color: var(--optionalColor);
    font-size: var(--fontSize);
    margin-bottom: 15px;
    line-height: 1.8;
}
p:last-child {
    margin-bottom: 0;
}
.ptb-100 {
    padding-top: 100px;
    padding-bottom: 100px;
}
.pt-100 {
    padding-top: 100px;
}
.pb-100 {
    padding-bottom: 100px;
}
.ptb-70 {
    padding-top: 70px;
    padding-bottom: 70px;
}
.pt-70 {
    padding-top: 70px;
}
.pb-70 {
    padding-bottom: 70px;
}
.container {
    max-width: 1230px;
}
.bg-ffffff {
    background-color: var(--whiteColor);
}
/*section-title*/
.section-title {
    max-width: 720px;
    text-align: center;
    position: relative;
    margin-left: auto;
    margin-right: auto;
    margin-bottom: 40px;
}
.section-title h2 {
    font-size: 26px;
    margin-bottom: 10px;
}
.section-title p {
    color: var(--blackColor);
}
.section-title.text-start {
    max-width: 100%;
    margin-left: 0;
    margin-right: 0;
}
.section-title.text-start p {
    max-width: 720px;
}
/*default-btn*/
.default-btn {
    border: none;
    padding: 12px 40px;
    display: inline-block;
    transition: var(--transition);
    box-shadow: unset !important;
    background-color: var(--mainColor);
    color: var(--whiteColor);
    font-weight: 600;
    font-size: var(--fontSize);
    font-family: var(--headingFontFamily);
}
.default-btn:hover {
    color: var(--whiteColor);
    background-color: var(--blackColor);
}
/*form-control*/
.form-control {
    border-radius: 0;
    background-color: var(--whiteColor) !important;
    box-shadow: unset !important;
    transition: var(--transition);
    border: none !important;
    height: 50px;
    padding-left: 15px;
    color: var(--blackColor);
    font-size: var(--fontSize);
    font-weight: 500;
}
.form-control::placeholder {
    color: var(--optionalColor);
    transition: var(--transition);
}
.form-control:focus::placeholder {
    color: transparent;
}
textarea.form-control {
    padding-top: 15px;
    height: auto;
}
